* {
  box-sizing: border-box;
  -ms-overflow-style: none;
  text-align: left;
}
::-webkit-scrollbar {
  display: none;
}

html {
  font-size: 18px;
}

@media only screen and (max-width: 770px) {
  html {
    font-size: 15px;
  }
  .txt-title {
    font-family: Arial;
    font-weight: bold;
    font-size: 1rem;
    padding: 10px 0px !important;
  }
  .list-item-answer {
    max-height: 40vh !important;
    overflow: scroll;
  }
}

@media only screen and (max-width: 575px) {
  html {
    font-size: 14px;
  }
  .txt-title {
    font-family: Arial;
    font-weight: bold;
    font-size: 1rem;
    padding: 8px 0px !important;
  }
  .list-item-answer {
    max-height: 45vh !important;
    overflow: scroll;
  }
  .img-add {
    width: 100px !important;
    height: 35px !important;
  }
  .left-item {
    overflow-y: "scroll";
    /* height: 30vh !important; */
  }
  .right-item {
    overflow-y: "scroll";
    height: 70vh !important;
  }
}

.container-table {
  background-color: #ffffff;
  padding: 0;
  /* height: 100vh; */
}

.left-container {
  background-color: rgb(0, 187, 255);
  border-width: 1px;
  border-style: solid;
  border-color: rgb(2, 137, 186);
}

.left-item {
  overflow-y: "scroll";
  height: 100vh;
}

.right-item {
  overflow-y: "scroll";
  height: 100vh;
}

.right-container {
  background-color: rgb(182, 221, 242);
  border-width: 1px;
  border-style: solid;
  border-color: rgb(2, 137, 186);
  padding-right: 50px !important;
}

.txt-title {
  font-family: Arial;
  font-weight: bold;
  font-size: 1rem;
  padding: 20px 0px;
}

.list-item {
  max-height: 80vh;
  overflow: scroll;
  /* padding: 0px 0px 25px 30px !important; */
  margin: 0px 0px 5px 0px;
}

.list-item-answer {
  max-height: 40vh;
  overflow: scroll;
}

.txt-item {
  font-family: Arial;
  margin-bottom: 15px;
  font-size: 1rem;
  display: block;
}

.txt-item-touched {
  font-family: Arial;
  margin-bottom: 12px;
  font-size: 1rem;
  font-weight: bold;
}

.txt-item-bold {
  font-family: Arial;
  font-weight: bold;
  font-size: 1rem;
  margin-top: 9px;
}

.two-button-container {
}

.button-left {
  padding: 5px !important;
  justify-content: flex-end;
  display: flex;
}

.button-right {
  padding: 5px !important;
  justify-content: flex-start;
  display: flex;
}

.form-item {
  /* display: flex;
  align-items: center; */
  background-color: yellow !important;
}

.question-item {
  margin: 0px 0px 5px 0px;
}

.button-image {
  background-color: "yellow" !important;
  align-items: "center";
  justify-content: "center";
  display: "flex";
}

.button-style {
  background-color: red;
}

/* selectQuestion */
.btn-primary-outline {
  background-color: transparent;
  border-color: transparent;
  box-shadow: none;
  display: block;
}

.img-add {
  max-width: "100%";
  max-height: "100%";
}

.modal {
  font-size: 12px;
}
.modal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 18px;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}
